.slick-dots-home li {
  width: 40px;
  height: 40px; }
  .slick-dots-home li img {
    filter: grayscale(100%); }

.slick-dots-home .slick-active img {
  filter: grayscale(0%);
  border: solid 2px #00b5e2;
  padding: 2px;
  transform: scale(1.2);
  transition: transform 0.2s linear; }

:local .img {
  width: 150px; }

@media (max-width: 576px) {
  :local .img {
    width: 120px; } }
